.welcomeImg {
  border-radius: 50%;
  margin: 30px auto 0px;
  border: 3px antiquewhite solid;
  box-shadow: 0px 20px 30px rgba(51, 50, 50, 0.6);
}
.invitationText {
  margin-top: 30px;
  font-family: inherit;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  font-weight: 600;
}

.button {
  margin: 20px auto 50px;
  background-color: #ebd8ff;
  box-shadow: 0px 10px 20px rgba(51, 50, 50, 0.25);
  border-radius: 10px;
  border-color: transparent;
  padding: 7px 20px;
  display: block;
  color: #373737;
  text-transform: uppercase;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
  transition: scale 300ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    scale: 1.07;
    background-color: #5cd3a8;
  }
}
